.App {
  text-align: center;
}

body {
  background-color: rgb(127, 246, 255);
}

h1 {
  font-size: 55px;
}

*{
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

#chooseColor {
  width: 10%;
  height: 50px;
  margin: auto;
  background-color: rgb(127, 246, 255);
  border: none;
}

.selectOptions {
  font-size: 40px;
  background-color: rgb(190, 246, 107);
  color: rgb(0, 0, 0);
  border: 2px solid white;
  width: fit-content;
  margin: auto;
}

.hide {
  visibility: hidden;
  height: 10px;
}

.show {
  visibility: visible;
}

.colorLabel {
  font-size: 35px;
  color: black;
  width: fit-content;
  margin: auto;
}

.addRoom {
  cursor: pointer;
  background-color: rgb(135, 147, 235);
  font-size: 270px;
  font-weight: bold;
  border: 3px solid black;
  color: black;
  width: 20%;
  margin: auto;
  border-radius: 100%;
}

.clickNav {
  cursor: pointer;
  background-color: rgb(22, 143, 219);
  font-size: 25px;
  color: white;
  width: 150px;
  margin: auto;
  border: 2px solid black;
  border-radius: 25px;
}

.clickbtn {
  cursor: pointer;
  background-color: rgb(22, 143, 219);
  font-size: 35px;
  color: white;
  width: fit-content;
  margin: auto;
  border-radius: 35px;
}

.flexboxContainer {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 30%;
  margin:auto;
  min-height: 50px;
  height: 50%;
}

.flexboxContainerLine {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  text-align: center;
  width: 70%;
  margin:auto;
  font-size: 35px;
}

.inputLabel {
  font-size: 25px;
  text-align: center;
  width: fit-content;
  margin:auto;
}

.redBG {
  font-size: 25px;
  text-align: center;
  width: fit-content;
  margin:auto;
  background-color: rgba(241, 38, 38, 0.5);
}

.greenBG {
  font-size: 25px;
  text-align: center;
  width: fit-content;
  margin:auto;
  background-color: rgba(38, 241, 143, 0.5);
}

.roomData {
  font-size: 25px;
  text-align: center;
  width: fit-content;
  margin:auto;
}

input {
  margin: 5px;
}


